'use strict';

var us = [];
var nl = 500;

exports.getUserStore = function () {
    return us;
};

exports.getNetworkLatency = function () {
    return nl;
};

exports.configure = function (netWorkLatency, userStore) {
    us = userStore;
    nl = netWorkLatency;
};

exports.authenticate = function (username, password) {
    if (us.length === 0) {
        throw new Error('Empty User Store');
    }
    return new Promise(function (resolve, reject) {
        setTimeout(function () {
            var user = us.find(function (user) {
                return user.username === username && user.password === password;
            });
            if (user) {
                delete user.password;
                resolve(user);
            } else {
                reject('Invalid User Credentials');
            }
        }, nl);
    });
};